<template>
  <div class="navigation-panel">
    <a class="name">Ankita Tank</a>
    <nav class="scene_nav">
      <ol class="scene_nav_list">
        <li class="scene_nav_item" v-for="(params, index) in maxScene" :key="index">
          <button
            class="scene_nav_button"
            @click="selectedView(index)"
            :class="{ 'o-active': scene-1 === index-1 }"
          ></button>
        </li>
      </ol>
    </nav>
  </div>
</template>

<script>
export default {
  components: {},
  data() {
    return {
      scene: 1,
      maxScene: 4
    };
  },
  methods: {
    selectedView(index) {
      this.scene = index;
      this.$emit("newViewSelected", index);
    }
  },
  mounted() {
    setTimeout(() => {
      this.scene = this.maxScene - 3;
    }, 1000);
  }
};
</script>

